@import "./Fonts.css";
@import "./responsive.css";

* {
    margin: 0;
    padding: 0;
}

.class_main_container {
    display: flex;
    width: 100%;
    font-family: 'Lato', sans-serif;
    overflow: auto;
}

.class_title {
    display: flex;
    align-items: center;
    width: 20%;
    justify-content: space-around;
    margin-top: 25px;
    font-family: 'Lato', sans-serif;
    font-weight: bolder;
    font-size: 40px;
    color: white;
}